@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.search-filters-wrapper {
  .search-top {
    .search-input-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: white;
      border: 1px solid pick-color('primary', 'fog');
      @include border-radius(8px);

      .search-input {
        padding: 8px 16px;
        border: none;
        background-color: transparent;
        font-size: 13px;
      }

      svg {
        color: pick-color('primary', 'web');
        width: 20px;
        height: 20px;
        margin-right: 16px;
      }
    }

    .save-search-wrapper {
      position: relative;

      .save-search-btn {
        padding: 8px 16px;
        border: none;
        font-size: 13px;
        color: white;
      }

      .save-search-dropdown-icon {
        padding: 8px;
        border-radius: 6px;
        height: 100%;
        border: none;

        mat-icon {
          font-size: 20px;
          width: 20px;
          height: 20px;
          color: white;
          transition: transform 0.2s;

          &.expanded {
            transform: rotate(-180deg);
          }
        }
      }

      .dropdown-wrapper {
        width: fit-content;
        left: unset;
        right: 0;

        .dropdown {
          li {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 20px;
            gap: 32px;

            &:not(:last-child) {
              border-bottom: 1px solid pick-color('primary', 'fog');
            }

            .save-state {
              height: 20px;
              display: flex;
              justify-content: center;
              flex-grow: 1;
              cursor: pointer;
              font-size: 14px;
              color: pick-color('primary', 'ash');
              font-weight: 500;

              svg {
                width: 20px;
                height: 20px;
                stroke: pick-color('primary', 'blue');
              }
            }

            .save-delete {
              display: flex;
              cursor: pointer;

              svg {
                width: 20px;
                height: 20px;
                stroke: pick-color('secondary', 'red');
              }
            }
          }
        }
      }
    }

    .reset-btn {
      cursor: pointer;
      @include circles(40px);
      background-color: color.adjust(pick-color('secondary', 'blue'), $lightness: 30%);
      border: none;

      svg {
        width: 20px;
        height: 20px;
        fill: pick-color('secondary', 'blue');
      }
    }
  }

  icon-button {
    span {
      line-height: 19px;
      font-size: 13px;
    }
  }

  .search-result-text {
    margin-top: 20px;
    margin-bottom: 16px;
    color: pick-color('primary', 'ash');
  }
}
